@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.thisinh-1 .container {
  padding-top: 50px;
  padding-bottom: 70px;
  background: #fef0e8;
}
@media (max-width: 576px) {
  .thisinh-1 .container {
    padding-bottom: 20px;
    padding-top: 25px !important;
  }
}
.thisinh-1 .title {
  font-size: 35px;
  line-height: 35px;
  margin-bottom: 30px;
  font-weight: 700;
  color: #333;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 576px) {
  .thisinh-1 .title {
    font-size: 24px;
    line-height: 24px;
    margin-bottom: 16px;
  }
}
.thisinh-1 .func-item {
  text-decoration: none;
  display: block;
  width: 100%;
}
.thisinh-1 .func-item.active .box-img, .thisinh-1 .func-item:hover .box-img {
  background: #faae2d;
}
.thisinh-1 .func-item.active figcaption, .thisinh-1 .func-item:hover figcaption {
  width: 100%;
  background: #f26f21;
  color: #fff;
}
.thisinh-1 .func-item:not(:last-child) {
  margin-bottom: 15px;
}
.thisinh-1 .func-item figure {
  display: flex;
}
.thisinh-1 .func-item .box-img {
  flex: 0 0 20%;
  max-width: 20%;
  min-height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 576px) {
  .thisinh-1 .func-item .box-img {
    padding: 20px;
  }
}
.thisinh-1 .func-item figcaption {
  font-weight: 700;
  font-size: 16px;
  width: 100%;
  text-transform: uppercase;
  padding: 15px;
  color: #ffffff;
  display: flex;
  align-items: center;
}
.thisinh-1 .func-item:nth-child(2n-1) .box-img {
  background: #f26f21;
}
.thisinh-1 .func-item:nth-child(2n-1) figcaption {
  background: #f58e51;
}
.thisinh-1 .func-item:nth-child(2n) .box-img {
  background: #005799;
}
.thisinh-1 .func-item:nth-child(2n) figcaption {
  background: #0074cc;
}
.thisinh-1 .btn-more {
  color: #f26f21;
  border-color: #f26f21;
  margin-top: 20px !important;
}
.thisinh-1 .btn-more:hover {
  background: #f26f21;
  color: #fff;
}
.thisinh-1 .btn-more span:after {
  margin-left: 4px;
}
@media (max-width: 992px) {
  .thisinh-1 .col-md-4 {
    margin-top: 60px;
  }
}
@media (min-width: 992px) {
  .thisinh-1 .func-item:hover {
    cursor: pointer;
  }
  .thisinh-1 .func-item:hover .box-img {
    background: #faae2d;
  }
  .thisinh-1 .func-item:hover figcaption {
    width: 100%;
    background: #f26f21;
  }
  .thisinh-1 .func-item:hover figcaption a {
    color: #fff;
  }
}
@media (max-width: 768px) {
  .thisinh-1 .func-item {
    width: 70%;
    margin: 0 auto;
  }
}
@media (max-width: 576px) {
  .thisinh-1 .col-lg-9 {
    margin-bottom: 0 !important;
  }
  .thisinh-1 .func-item {
    width: 100%;
  }
  .thisinh-1 .func-item .box-img {
    flex: 0 0 25%;
    max-width: 25%;
  }
  .thisinh-1 .func-item figcaption {
    font-size: 13px;
  }
}

